     #screen {
         width: 300px;
         height: 200px;
         text-align: center;
         user-select: none;
     }
     #screen.waiting {
         background-color: skyblue;
     }
     #screen.ready {
         background-color: violet;
         color: white;
     }
     #screen.now {
         background-color: peru;
     }
